.aboutus {
  min-height: 433px;
  background-image: url(/img/banners/aboutus-background-desktop@2x.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.aboutus__container {
  padding-top: 120px;
  padding-bottom: 120px;
  margin-bottom: 140px;
}

.aboutus__title {
  font-weight: 500;
  font-size: 32px;
  line-height: 42px;
  text-align: center;
  max-width: 760px;
  margin: 0 auto;
}

.aboutus__more {
  display: block;
  text-align: center;
  margin: 50px auto 0;
}

.joinus__container {
  display: flex;
  align-items: center;
  margin-bottom: 140px;
}

.joinus__info {
  padding: 141px 0 141px 71px;
}

.joinus__text {
  margin-top: 30px;
}

.joinus__subscribe {
  margin-top: 40px;
  display: block;
}

.collage {
  display: flex;
  max-width: 715px;
  flex-wrap: wrap;
}

.collage__item {
  width: fit-content;
  height: fit-content;
}

.contacts__container {
  margin-bottom: 140px;
}

.contacts__content {
  position: relative;
}

.contacts__text-container {
  padding: 100px 220px 100px 100px;
  max-width: 670px;
  background-color: #F4F1ED;
}

.contacts__text {
  margin-top: 40px;
}
.contacts__text p + p {
  margin-top: 23px;
}
.contacts__text span {
  font-weight: 500;
}
.contacts__text span + a {
  margin-top: 20px;
  margin-bottom: 35px;
}

.contacts__map-container {
  position: absolute;
  right: 0;
  top: 66px;
  max-width: 670px;
  width: 58%;
}

.contacts__map {
  max-width: 670px;
  max-height: 422px;
}

.social {
  display: flex;
}

.social__item:not(:last-child) {
  margin-right: 30px;
}

.footer {
  background-color: #F4F1ED;
  min-height: 141px;
}

.footer-container {
  padding-top: 60px;
  padding-bottom: 60px;
  display: flex;
}

.footer__logo {
  margin-right: 69px;
}

.footer__social {
  margin-left: auto;
}

.copyright {
  border-top: 1px solid #D1D1D1;
}

.copyright__container {
  display: flex;
  justify-content: space-between;
  padding-top: 22px;
  padding-bottom: 22px;
}

.catalog__container {
  margin-bottom: 140px;
}

.catalog__head {
  display: flex;
  justify-content: space-between;
  padding-top: 48px;
}

.catalog__header {
  margin-bottom: 60px;
  font-size: 36px;
  line-height: 47px;
}

.catalog__slider {
  position: relative;
}

.catalog__slide {
  max-height: 1110px;
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}

.catalog__button--prev {
  position: absolute;
  bottom: -43px;
  right: 57px;
}

.catalog__button--next {
  position: absolute;
  bottom: -43px;
  right: 0;
}

.filter {
  display: flex;
}

.filter__column {
  padding: 0;
}

.product {
  position: relative;
}

.product__text-container {
  position: absolute;
  bottom: 0;
  padding: 20px;
  width: 100%;
}

.product__text {
  display: flex;
  justify-content: space-between;
}

.product__name {
  font-weight: 500;
  font-size: 16px;
  line-height: 23px;
  font-family: "Raleway";
}

.product__price {
  font-weight: 500;
  font-size: 13px;
  line-height: 19px;
}

.product__description {
  font-size: 13px;
  line-height: 19px;
  color: #9A9DA0;
}

.product__value {
  font-size: 13px;
  line-height: 19px;
  color: #9A9DA0;
}

.recently__container {
  margin-bottom: 140px;
}

.recently__header {
  text-align: center;
  margin-bottom: 60px;
}

.recently__slider {
  position: relative;
}

.recently__button--prev {
  position: absolute;
  bottom: -43px;
  right: 43px;
}

.recently__button--next {
  position: absolute;
  right: -15px;
}

.recently__list {
  display: flex;
  justify-content: space-between;
  column-gap: 30px;
  padding-top: 60px;
}

.card__container {
  padding-top: 40px;
  margin-bottom: 140px;
}

.card__content {
  display: flex;
  justify-content: space-between;
}

.card__image-container {
  max-width: 630px;
  max-height: 647px;
}

.card__text-container {
  max-width: 370px;
  padding-top: 50px;
}
.card__text-container fieldset {
  padding: 0;
}

.card-text__header {
  margin-bottom: 15px;
  font-size: 32px;
  line-height: 40px;
}

.card-text__subtitle {
  margin-bottom: 45px;
  display: block;
  font-size: 16px;
  line-height: 23px;
  color: #9A9DA0;
}

.card-text__description {
  margin-bottom: 45px;
}
.card-text__description p + p {
  margin-top: 15px;
}

.card__accordion {
  padding-left: 0;
  padding-right: 0;
}

.card__label {
  font-size: 18px;
  line-height: 26px;
  font-weight: 500;
}

.card__price {
  font-weight: 500;
  font-size: 26px;
  line-height: 38px;
}

.cognate__container {
  margin-bottom: 140px;
}

.cognate__header {
  text-align: center;
  margin-bottom: 60px;
}

.cognate__slider {
  position: relative;
}

.cognate__button--prev {
  position: absolute;
  bottom: -43px;
  right: 43px;
}

.cognate__button--next {
  position: absolute;
  right: -15px;
}

.cognate__list {
  display: flex;
  justify-content: space-between;
  column-gap: 30px;
  padding-top: 60px;
}

.accordion {
  margin: 0 auto 50px auto;
}
.accordion .accordion {
  width: 90%;
  margin: 0 auto;
}
.accordion__element:last-child {
  margin-bottom: 0;
}
.accordion__element {
  border-top: 1px solid #B3BAC1;
}
.accordion__element.is-active > .accordion__button {
  opacity: 0.5;
}
.accordion__button {
  width: 100%;
  padding: 18px;
  padding-left: 0;
  padding-right: 0;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}
.accordion__wrapper {
  padding: 10px;
}

[data-accordion=element].is-active > [data-accordion=content] {
  max-height: 100%;
}

[data-accordion=parent].no-transition [data-accordion=content] {
  transition: none;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s;
  overflow: hidden;
}

